.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    min-height: 100vh;

}

#app-top-section {
    width: 100%;
}
#home-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    justify-self: flex-start;
    align-self: flex-end;
    height: 30px;
    border: 1px solid black;
    width: 99%;
}

#home-nav span{
    width: 20%;
}

#home-nav a {
    font-size: 22px;
    color: black;
}